body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;scroll-behavior:smooth}
.shop{padding:60px 40px;min-height:100vh;display:flex;flex-direction:column;align-items:center;background-color:#323232}.shop .shop__header{font-family:"Architects Daughter",cursive;text-transform:uppercase;letter-spacing:2vw;font-size:calc(2vw + 25px);color:#fff;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;background-color:#000}.shop .shop__products{display:flex;flex-wrap:wrap;justify-content:center;grid-gap:20px;gap:20px}


.main .main__header{padding:60px 40px;background:url("https://wallpaperaccess.com/full/1585697.jpg") no-repeat right center;background-size:cover;background-color:rgba(0,0,0,.6);background-blend-mode:multiply;height:100vh}.main .main__header .main__header__title{font-family:"Montserrat",sans-serif;text-transform:uppercase;letter-spacing:calc(2vw + 20px);font-size:calc(3vw + 25px);color:#fff;position:relative;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);border:3px solid #fff;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.main .main__header .main__header__title .main__header__title__first-letter{color:orange;background-color:#000}

